import {
  Fragment,
  MouseEventHandler,
  useCallback,
  useEffect,
  // useState,
} from "react";
import { Helmet } from "react-helmet-async";
import {
  Card,
  CardContent,
  IconButton,
  List,
  Link,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Button,
  Box,
} from "@mui/material";
import FacebookLogin, {
  LoginResponse,
} from "@greatsumini/react-facebook-login";

import { useGlobal } from "Contexts/App";
import { useSnacks } from "Contexts/Snack";
import { useSocialMediasApi, useSocialMediasData } from "Contexts/SocialMedias";
import {
  CardSX,
  CardContentSX,
  CardHeading,
  Content,
  ContentHeader,
  ContentInner,
  HeaderTitle,
  ContentWrapper,
  CardList,
  CardListItem,
  CardListTitle,
  FbButtonSx,
  InButtonSx,
  TwButtonSx,
} from "Containers/BusinessSettings/LayoutComponents";
import { IAccounts } from "Shared/Interfaces/SocialMedias";
import { formatFB_IGAccountsArray } from "Utils";
import SocialMediaApi from "Apis/SocialMediaApi";
import {
  Facebook,
  Instagram,
  LinkedIn,
  LeftArrow,
  Twitter,
} from "Assets/Icons";
import { AppLink } from "Components/Common";

import FaceBookLinking from "./SocialMediaLinking/FaceBookLinking";
import ViewPermissions from "./SocialMediaLinking/ViewPermissions";
import LinkedInLinking from "./SocialMediaLinking/LinkedInLinking";

const SocialMediaLinking = () => {
  const pageTitle = "Social Media Linking";
  const { isMobile, businessId } = useGlobal();
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { connectedAccounts, unconnectedAccounts, platform } =
    useSocialMediasData();
  const {
    setConnectedAccounts,
    setUnconnectedAccounts,
    setPlatform,
    setPermissions,
  } = useSocialMediasApi();
  const HandleFBResponse = async (response: LoginResponse["authResponse"]) => {
    if (!response) {
      return;
    }
    if (!("accessToken" in response) && !("userID" in response)) {
      setErrorMessage("Problem logging in with Facebook!");
      return;
    }

    const fbData = {
      businessId,
      fb_user_id: response.userID,
      short_lived_token: response.accessToken,
    };

    try {
      const res = await SocialMediaApi.saveFBUserData(businessId, fbData);
      if (res.success) {
        setPlatform("facebook");
      } else {
        setErrorMessage(
          res.messages ? res.messages.errors[0].message : res.message
        );
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };
  const handleTWLogin: MouseEventHandler = () => {
    localStorage.setItem("esUpdateTWAccount", "true");
    window.open(
      `${process.env.REACT_APP_API_URL}/${process.env.REACT_APP_API_VER}/twitter/login`,
      "_self"
    );
  };
  const handleLILogin: MouseEventHandler = () => {
    localStorage.setItem("esUpdateLIAccount", "true");
    window.open(
      `${process.env.REACT_APP_API_URL}/${process.env.REACT_APP_API_VER}/setup/linkedin/login`,
      "_self"
    );
  };
  const handleUnlinkAccounts = async (platform: string) => {
    try {
      const res = await SocialMediaApi.unlinkSocialMediaAccount({
        platform: platform.toUpperCase(),
        product: "curate",
      });
      if (res.success) {
        const unlinkAccountMsg: string = `${`${platform
          .charAt(0)
          .toUpperCase()}${platform.slice(1)}`} unlinked!`;
        setSuccessMessage(unlinkAccountMsg);
        let newConnectedAccounts;
        if (platform === "facebook" || platform === "instagram") {
          newConnectedAccounts = connectedAccounts.filter(
            (acc) => acc.platform !== platform
          );
          setConnectedAccounts(newConnectedAccounts);
          if (!unconnectedAccounts.includes("facebook and instagram")) {
            setUnconnectedAccounts(
              formatFB_IGAccountsArray([...unconnectedAccounts, platform])
            );
          }
        } else {
          newConnectedAccounts = connectedAccounts.filter(
            (acc) => acc.platform !== platform
          );
          setConnectedAccounts(newConnectedAccounts);
          setUnconnectedAccounts(
            formatFB_IGAccountsArray([...unconnectedAccounts, platform])
          );
        }
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };
  const loadSetupInfo = useCallback(async () => {
    try {
      const res = await SocialMediaApi.getSetupInfo();
      if (!res.success) {
        throw Error(res.message);
      } else {
        setConnectedAccounts(res.payload.connectedAccounts);
        setUnconnectedAccounts(
          formatFB_IGAccountsArray(res.payload.unconnectedAccounts)
        );
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [platform]);

  useEffect(() => {
    loadSetupInfo();
  }, [loadSetupInfo]);

  return (
    <Fragment>
      <Helmet>
        <title>{pageTitle}</title>
      </Helmet>
      <Content>
        {isMobile && (
          <ContentHeader>
            <IconButton
              component={AppLink}
              to="/business-settings"
              sx={{ width: 32, height: 32 }}
            >
              <LeftArrow />
            </IconButton>
            <HeaderTitle>{pageTitle}</HeaderTitle>
          </ContentHeader>
        )}
        <ContentInner>
          <ContentWrapper>
            {connectedAccounts.length > 0 && (
              <Card elevation={0} sx={CardSX}>
                <CardContent sx={CardContentSX}>
                  <CardHeading
                    sx={{
                      borderBottom: "1px solid #24272C1f",
                      pb: 2,
                    }}
                  >
                    Linked Accounts
                  </CardHeading>
                  <List>
                    {connectedAccounts.map(
                      ({
                        platform,
                        accountProfileIMG,
                        accountID,
                        accountName,
                      }: IAccounts) =>
                        platform === "facebook and instagram" ? (
                          <ListItem
                            sx={{
                              alignItems: "flex-start",
                              p: 0,
                              mb: 3.75,
                              flexWrap: "wrap",
                            }}
                          >
                            <ListItemText primary={platform} />
                            <ListItemAvatar>
                              <Facebook />
                              <Instagram />
                            </ListItemAvatar>
                            <div className="linked-account">
                              <figure>
                                <img
                                  src={"logoURL"}
                                  alt={"logoURL"}
                                  height={40}
                                  width={40}
                                />
                              </figure>
                              <span className="account-name">{"xyz"}</span>
                            </div>
                            <div className="actions">
                              <Link underline="always" onClick={() => {}}>
                                {"Unlink Account"}
                              </Link>
                              <Link
                                underline="always"
                                onClick={() => {
                                  setPermissions({
                                    show: true,
                                    provider: "facebook",
                                  });
                                }}
                              >
                                {"View Permissions"}
                              </Link>
                            </div>
                          </ListItem>
                        ) : (
                          <ListItem
                            sx={{
                              alignItems: "flex-start",
                              p: 0,
                              mb: 3.75,
                              flexWrap: "wrap",
                            }}
                            key={accountID}
                          >
                            <ListItemText
                              primary={`${platform
                                .charAt(0)
                                .toUpperCase()}${platform.slice(1)}`}
                              sx={{ mb: 1.5 }}
                            />
                            <ListItemAvatar
                              sx={{
                                display: "flex",
                                columnGap: 1.25,
                                mt: 0.5,
                                justifyContent: "end",
                              }}
                            >
                              <img
                                src={`/static/images/svg/${platform}.svg`}
                                alt={platform}
                                height={18}
                                width={18}
                              />
                            </ListItemAvatar>
                            <Box
                              sx={{
                                width: "100%",
                                display: "flex",
                                columnGap: 1.5,
                                alignItems: "center",
                                mb: 1,
                                justifyContent: "flex-start",
                              }}
                            >
                              <Box component="figure" sx={{ display: "flex" }}>
                                <img
                                  src={accountProfileIMG}
                                  alt={accountProfileIMG}
                                  height={40}
                                  width={40}
                                />
                              </Box>
                              <span className="account-name">
                                {accountName}
                              </span>
                            </Box>
                            <Box
                              sx={{
                                display: "flex",
                                columnGap: 4,
                                justifyContent: "space-between",
                              }}
                            >
                              <Link
                                underline="always"
                                onClick={() => handleUnlinkAccounts(platform)}
                                sx={{
                                  color: "#656F7A",
                                  cursor: "pointer",
                                  fontSize: 12,
                                  fontWeight: 500,
                                  lineHeight: "14px",
                                  textDecorationColor: "#656F7A",
                                }}
                              >
                                {"Unlink Account"}
                              </Link>
                              <Link
                                underline="always"
                                onClick={() =>
                                  setPermissions({
                                    show: true,
                                    provider: platform,
                                  })
                                }
                                sx={{
                                  color: "#656F7A",
                                  cursor: "pointer",
                                  fontSize: 12,
                                  fontWeight: 500,
                                  lineHeight: "14px",
                                  textDecorationColor: "#656F7A",
                                }}
                              >
                                {"View Permissions"}
                              </Link>
                            </Box>
                          </ListItem>
                        )
                    )}
                  </List>
                </CardContent>
              </Card>
            )}
            {unconnectedAccounts.length > 0 && (
              <Card elevation={0} sx={CardSX}>
                <CardContent sx={CardContentSX}>
                  <CardHeading
                    sx={{
                      borderBottom: "1px solid #24272C1f",
                      pb: 2,
                    }}
                  >
                    Link More Accounts
                  </CardHeading>
                  <CardList>
                    {unconnectedAccounts.map((account: string, idx: number) => {
                      return (
                        <div key={idx}>
                          {(account === "facebook" ||
                            account === "instagram") && (
                            <CardListItem>
                              <CardListTitle>
                                {`${account
                                  .charAt(0)
                                  .toUpperCase()}${account.slice(1)}`}
                              </CardListTitle>
                              <FacebookLogin
                                appId={process.env.REACT_APP_APP_ID ?? ""}
                                fields="name,email,picture"
                                scope="pages_manage_cta,pages_read_engagement,pages_manage_posts,instagram_basic,instagram_manage_comments,read_insights,instagram_content_publish,instagram_manage_insights,pages_manage_engagement"
                                onSuccess={HandleFBResponse}
                                render={(renderProps: any) => (
                                  <Button
                                    onClick={() => {
                                      renderProps.onClick();
                                    }}
                                    sx={FbButtonSx}
                                  >
                                    <Facebook />
                                    <span className="label">
                                      {"Sign In with Facebook"}
                                    </span>
                                  </Button>
                                )}
                              />
                            </CardListItem>
                          )}
                          {account === "facebook and instagram" && (
                            <CardListItem>
                              <CardListTitle>
                                {`Facebook and Instagram`}
                              </CardListTitle>
                              <FacebookLogin
                                appId={process.env.REACT_APP_APP_ID ?? ""}
                                fields="name,email,picture"
                                scope="pages_manage_cta,pages_read_engagement,pages_manage_posts,instagram_basic,instagram_manage_comments,read_insights,instagram_content_publish,instagram_manage_insights,pages_manage_engagement"
                                onSuccess={HandleFBResponse}
                                render={(renderProps: any) => (
                                  <Button
                                    onClick={() => {
                                      renderProps.onClick();
                                    }}
                                    sx={FbButtonSx}
                                  >
                                    <Facebook bg="#FFFFFF" />
                                    <span className="label">
                                      {"Sign In with Facebook"}
                                    </span>
                                  </Button>
                                )}
                              />
                            </CardListItem>
                          )}
                          {false && account === "twitter" && (
                            <CardListItem>
                              <CardListTitle>{"Twitter"}</CardListTitle>
                              <Button onClick={handleTWLogin} sx={TwButtonSx}>
                                <Twitter />
                                <span className="label">
                                  {"Sign in with Twitter"}
                                </span>
                              </Button>
                            </CardListItem>
                          )}
                          {account === "linkedin" && (
                            <CardListItem>
                              <CardListTitle>{"LinkedIn"}</CardListTitle>
                              <Button onClick={handleLILogin} sx={InButtonSx}>
                                <LinkedIn />
                                <span className="label">
                                  {"Sign in with LinkedIn"}
                                </span>
                              </Button>
                            </CardListItem>
                          )}
                        </div>
                      );
                    })}
                  </CardList>
                </CardContent>
              </Card>
            )}
          </ContentWrapper>
        </ContentInner>
      </Content>
      <ViewPermissions
        closeDrawer={() =>
          setPermissions({
            show: false,
            provider: "",
          })
        }
      />
      <LinkedInLinking
        open={platform === "linkedin"}
        closeDrawer={() => setPlatform(null)}
      />
      <FaceBookLinking
        open={platform === "facebook"}
        closeDrawer={() => setPlatform(null)}
      />
    </Fragment>
  );
};

export default SocialMediaLinking;
