import { Drawer, IconButton } from "@mui/material";

import {
  ContentHeader,
  DrawerInner,
  DrawerWrapper,
  HeaderSubTitle,
  HeaderTitle,
  HeaderTitles,
} from "Containers/BusinessSettings/LayoutComponents";
import { useSocialMediasData } from "Contexts/SocialMedias";
import { isBackDropOrEscapeKey } from "Utils";

import { LeftArrow } from "Assets/Icons";
import Permissions from "./Permissions";

interface ViewPermissionsProps {
  anchor?: "bottom" | "left" | "right" | "top";
  closeDrawer: () => void;
}

const ViewPermissions = ({
  anchor = "right",
  closeDrawer,
}: ViewPermissionsProps) => {
  const { permissions } = useSocialMediasData();
  const getSubtitle = () =>
    ({
      twitter: "Twitter",
      linkedin: "LinkedIn",
      facebook: "Facebook & Instagram",
      instagram: "Facebook & Instagram",
      "": "Facebook & Instagram",
    }[permissions.provider]);

  return (
    <Drawer
      anchor={anchor}
      open={permissions.show}
      onClose={(_, reason) => !isBackDropOrEscapeKey(reason) && closeDrawer()}
      sx={{
        zIndex: "modal",
      }}
    >
      <DrawerWrapper sx={{ width: { xs: "100vw", md: 360 } }}>
        <ContentHeader>
          <IconButton
            onClick={() => closeDrawer()}
            sx={{ width: 32, height: 32 }}
          >
            <LeftArrow />
          </IconButton>
          <HeaderTitles>
            <HeaderTitle>{`Permissions Granted`}</HeaderTitle>
            <HeaderSubTitle>{getSubtitle()}</HeaderSubTitle>
          </HeaderTitles>
        </ContentHeader>
        <DrawerInner sx={{ pb: 2.5 }}>
          <Permissions platform={permissions.provider} />
        </DrawerInner>
      </DrawerWrapper>
    </Drawer>
  );
};

export default ViewPermissions;
