import moment from "moment";
import ReactHtmlParser from "html-react-parser";

export function getRandomColor() {
  var rgb = [];

  for (var i = 0; i < 3; i++) {
    rgb.push(Math.floor(Math.random() * 255));
  }

  return "rgb(" + rgb.join(",") + ", 0.5)";
}

export function secondsToHms(d: number) {
  d = Number(d);
  let h = Math.floor(d / 3600);
  let m = Math.floor((d % 3600) / 60);
  let s = Math.floor((d % 3600) % 60);

  let hDisplay = h > 0 ? h + (h === 1 ? " hour " : " hours ") : "";
  let mDisplay = m > 0 ? m + (m === 1 ? " minute " : " minutes ") : "";
  let sDisplay = s > 0 ? s + (s === 1 ? " second" : " seconds") : "";
  return hDisplay + mDisplay + sDisplay;
}

export const isBackDropOrEscapeKey = (reason: string) =>
  reason === "backdropClick" || reason === "escapeKeyDown";

export const monthFormat = (month: string) => moment(month).format("MMM YYYY");

export const formatFB_IGAccountsArray = (accounts: string[]): string[] => {
  let withChangedFBAccounts: string[] = [];

  if (accounts.includes("facebook")) {
    withChangedFBAccounts = accounts.filter((acc) => acc !== "facebook");
  }

  if (accounts.includes("instagram")) {
    withChangedFBAccounts = withChangedFBAccounts.filter(
      (acc) => acc !== "instagram"
    );
  }

  if (accounts.includes("facebook") || accounts.includes("instagram")) {
    withChangedFBAccounts.unshift("facebook and instagram");
  }

  if (
    accounts.includes("facebook and instagram") &&
    !withChangedFBAccounts.includes("facebook and instagram")
  ) {
    withChangedFBAccounts.push("facebook and instagram");
  }

  if (
    accounts.includes("twitter") &&
    !withChangedFBAccounts.includes("twitter")
  ) {
    withChangedFBAccounts.push("twitter");
  }

  if (
    accounts.includes("linkedin") &&
    !withChangedFBAccounts.includes("linkedin")
  ) {
    withChangedFBAccounts.push("linkedin");
  }

  return withChangedFBAccounts;
};

export const userInitials = (name: string) => {
  return {
    children: `${name.split(" ")[0][0]}${name.split(" ")[1][0]}`,
  };
};

export const formatMessage = (message: string) =>
  ReactHtmlParser(message.replaceAll(/(?:\r\n|\r|\n)/g, "<br />"));
