import { Fragment, useCallback, useEffect, useState } from "react";
import {
  Drawer,
  Divider,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  ListItemAvatar,
  ListItemIcon,
  Skeleton,
} from "@mui/material";
import { CircleOutlined, CheckCircleOutlined } from "@mui/icons-material";

import { useSnacks } from "Contexts/Snack";
import {
  ActionArea,
  ActionButton,
  ContentHeader,
  DrawerInner,
  DrawerWrapper,
  HeaderTitle,
} from "Containers/BusinessSettings/LayoutComponents";
import { useSocialMediasApi } from "Contexts/SocialMedias";
import { isBackDropOrEscapeKey } from "Utils";

import { LeftArrow } from "Assets/Icons";
import SocialMediaApi from "Apis/SocialMediaApi";

type FaceBookLinkingProps = {
  anchor?: "bottom" | "left" | "right" | "top";
  open: boolean;
  closeDrawer: () => void;
};

const FaceBookLinking = ({
  anchor = "right",
  open,
  closeDrawer,
}: FaceBookLinkingProps) => {
  // contexts
  const { setErrorMessage } = useSnacks();
  const { setPlatform } = useSocialMediasApi();
  // States
  const [
    stage, // ? 1 = facebook & 2 = instagram
    setStage,
  ] = useState(1);
  const [selected, setSelected] = useState<number | null>(null);
  const [FBPages, setFBPages] = useState<any[]>([]);
  const [noUserPagesFoundError, setNoUserPagesFoundError] =
    useState<boolean>(false);
  const [IGAccount, setIGAccount] = useState<any | null>(null);
  const ListItemImage = ({ src, alt }: { src: string; alt: string }) => {
    const [loading, setLoading] = useState(true);
    const onImageLoad = () => setLoading(false);

    return (
      <ListItemAvatar>
        {loading && (
          <Skeleton
            variant="rectangular"
            className="avatar"
            width={50}
            height={50}
          />
        )}
        <img
          src={src}
          alt={alt}
          onLoad={onImageLoad}
          className={`avatar${loading ? " hidden" : ""}`}
          width={loading ? 0 : 50}
          height={loading ? 0 : 50}
        />
      </ListItemAvatar>
    );
  };
  // Event handlers
  const handleConfirmItemSelect = async () => {
    try {
      // fetch the page details from selected page id
      if (stage === 1) {
        // handle select fb page
        const selectedPage = FBPages.find(
          (fbPage) => fbPage.page_id === selected
        );
        // save the page data
        const page = {
          img_url: selectedPage.profile_image_url,
          page_id: selectedPage.page_id,
          page_name: selectedPage.page_name,
        };
        const res = await SocialMediaApi.saveFBPageData(page);
        if (!res.success) {
          throw Error(res.message);
        }
        if (res.success) {
          setStage(2);
          setSelected(null);
        }
      } else if (stage === 2) {
        // handle select ig account
        if (!IGAccount) {
          return setErrorMessage("Please select an instagram account");
        }
        const igReqData = {
          account_id: IGAccount.igAccID,
          profile_picture: IGAccount.image,
          name: IGAccount.name,
          username: IGAccount.username,
        };
        const res = await SocialMediaApi.saveIgAccountData(igReqData);
        if (!res.success) {
          throw Error(res.message);
        }
        if (res.success) {
          // close the drawer
          setPlatform(null);
          setStage(1);
          setSelected(null);
        }
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };
  const linkingCall = useCallback(async () => {
    try {
      if (open) {
        if (stage === 1) {
          const res = await SocialMediaApi.getBusinessFBPages();
          if (!res.success) {
            throw Error(res.message);
          }
          const { payload: fb_pages } = res;
          if (!fb_pages.length) {
            return setNoUserPagesFoundError(true);
          }
          setFBPages(fb_pages);
        } else if (stage === 2) {
          const res = await SocialMediaApi.getIgAccountData();
          if (!res.success) {
            throw Error(res.message);
          }
          if (res.message === "not found") {
            window.open(res.payload, "_blank");
          } else {
            const igAccData = {
              igAccID: res.payload.account_id,
              name: res.payload.name,
              username: res.payload.username,
              image: res.payload.profile_picture,
              // selected: false,
            };
            setIGAccount(igAccData);
          }
        }
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [open, stage]);

  // component effects
  useEffect(() => {
    linkingCall();
  }, [linkingCall]);

  return (
    <Drawer
      anchor={anchor}
      open={open}
      onClose={(_, reason) => {
        if (!isBackDropOrEscapeKey(reason)) {
          closeDrawer();
        }
      }}
      sx={{ zIndex: "modal" }}
    >
      <DrawerWrapper sx={{ width: { xs: "100vw", md: 360 } }}>
        <ContentHeader>
          <IconButton
            onClick={() => closeDrawer()}
            sx={{ width: 32, height: 32 }}
          >
            <LeftArrow />
          </IconButton>
          <HeaderTitle>
            {stage === 1 ? "Select Facebook Page" : "Select Instagram Account"}
          </HeaderTitle>
        </ContentHeader>
        <DrawerInner>
          {stage === 1 ? (
            <Fragment>
              {noUserPagesFoundError && (
                <p>
                  Sorry, we couldn't find any Facebook page associated with your
                  account.
                </p>
              )}
              {FBPages.length > 0 && (
                <List>
                  {FBPages.map((page) => (
                    <div key={page.page_id}>
                      <ListItem sx={{ p: 0 }}>
                        <ListItemButton
                          onClick={() =>
                            setSelected(selected ? null : page.page_id)
                          }
                        >
                          {/* <ListItemImage src={"image"} alt={"name"} /> */}
                          <ListItemImage
                            src={page.profile_image_url}
                            alt={"FB page image url"}
                          />
                          <ListItemText
                            primary={page.page_name}
                            secondary={
                              page.isUsedByBusiness
                                ? "Page already in use"
                                : undefined
                            }
                          />
                          <ListItemIcon>
                            {selected === page.page_id ? (
                              <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                            ) : (
                              <CircleOutlined sx={{ color: "#F75D34" }} />
                            )}
                          </ListItemIcon>
                        </ListItemButton>
                      </ListItem>
                      <Divider />
                    </div>
                  ))}
                </List>
              )}
            </Fragment>
          ) : (
            <List>
              {IGAccount && (
                <Fragment>
                  <ListItem sx={{ p: 0 }}>
                    <ListItemButton
                      onClick={() =>
                        setSelected(selected ? null : IGAccount.igAccID)
                      }
                    >
                      <ListItemImage src={IGAccount.image} alt={"name"} />
                      <ListItemText primary={IGAccount.name} />
                      <ListItemIcon>
                        {selected === IGAccount.igAccID ? (
                          <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                        ) : (
                          <CircleOutlined sx={{ color: "#24272C80" }} />
                        )}
                      </ListItemIcon>
                    </ListItemButton>
                  </ListItem>
                  <Divider />
                </Fragment>
              )}
            </List>
          )}
          <ActionArea>
            <ActionButton
              variant="contained"
              style={{
                marginTop: "40px",
                marginBottom: "30px",
                marginLeft: 0,
              }}
              disabled={selected === null}
              onClick={handleConfirmItemSelect}
            >
              {stage === 1
                ? "Link & Continue to Instagram"
                : "Link Instagram Account"}
            </ActionButton>
          </ActionArea>
        </DrawerInner>
      </DrawerWrapper>
    </Drawer>
  );
};

export default FaceBookLinking;
