import { useCallback, useEffect, useState } from "react";
import {
  CircularProgress,
  Divider,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemAvatar,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Skeleton,
} from "@mui/material";

import {
  ActionArea,
  ActionButton,
  ContentHeader,
  DrawerInner,
  DrawerWrapper,
  HeaderTitle,
  LoaderWrapper,
} from "Containers/BusinessSettings/LayoutComponents";
import { useSocialMediasApi, useSocialMediasData } from "Contexts/SocialMedias";
import { useSnacks } from "Contexts/Snack";
import { isBackDropOrEscapeKey } from "Utils";

import { LeftArrow } from "Assets/Icons";
import SocialMediaApi from "Apis/SocialMediaApi";
import { CheckCircleOutlined, CircleOutlined } from "@mui/icons-material";

interface LinkedInLinkingProps {
  anchor?: "bottom" | "left" | "right" | "top";
  open: boolean;
  closeDrawer: () => void;
}

const LinkedInLinking = ({
  anchor = "right",
  open,
  closeDrawer,
}: LinkedInLinkingProps) => {
  const { LIUser } = useSocialMediasData();
  const { setLIUser, setPlatform } = useSocialMediasApi();
  const { setErrorMessage } = useSnacks();
  const [showLoading, setShowLoading] = useState<boolean>(true);
  const [selected, setSelected] = useState<string | null>(null);
  const [pages, setPages] = useState<any[]>([]);
  const [noUserOrgPagesFound, setNoUserOrgPagesFound] =
    useState<boolean>(false);
  const [disableBtn, setDisableBtn] = useState<boolean>(true);
  const ListItemImage = ({ src, alt }: { src: string; alt: string }) => {
    const [loading, setLoading] = useState(true);
    const onImageLoad = () => setLoading(false);

    return (
      <ListItemAvatar>
        {loading && (
          <Skeleton
            variant="rectangular"
            className="avatar"
            width={50}
            height={50}
          />
        )}
        <img
          src={src}
          alt={alt}
          onLoad={onImageLoad}
          className={`avatar${loading ? " hidden" : ""}`}
          width={loading ? 0 : 50}
          height={loading ? 0 : 50}
        />
      </ListItemAvatar>
    );
  };
  const handleLISetup = async () => {
    try {
      const liOrg = pages.find((page) => page.orgID === selected);
      if (!liOrg) {
        throw Error("Error finding organization!");
      }
      if (LIUser === null) {
        throw Error("LinkedIn User data not found.");
      }
      const { orgID, image: orgLogo, name: orgName } = liOrg;
      const { token, tokenSecret, id: userID } = LIUser;
      const liRequestData = {
        orgID,
        orgLogo,
        orgName,
        token,
        tokenSecret,
        linkedinUserId: userID,
      };
      const res = await SocialMediaApi.setLinkedinBusiness(liRequestData);
      if (!res.success) {
        throw Error(res.message);
      }
      setPlatform(null);
      setLIUser(null);
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };
  const fetchLiUserPages = useCallback(async () => {
    try {
      if (LIUser) {
        const { token: liToken } = LIUser;
        const res = await SocialMediaApi.getLiUserPages({ liToken });
        if (!res.success) {
          throw Error(res.message);
        }
        const { payload } = res;
        console.log("fetch li user pages data: ", payload);
        if (!payload) {
          return setErrorMessage("No organization data found in response!");
        }
        if (payload && !payload.length) {
          setShowLoading(false);
          setErrorMessage("No user linkedin organization pages found!");
          setNoUserOrgPagesFound(true);
          setDisableBtn(true);
        } else {
          // render the pages in the UI by setting state
          const liOrgData = payload.map((org: any) => {
            return {
              orgID: org.id,
              image: org.logo,
              name: org.name,
              selected: false,
              //inUse: org.inUse,
              inUse: false,
            };
          });
          setShowLoading(false);
          setDisableBtn(false);
          setPages(liOrgData);
        }
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [LIUser]);

  useEffect(() => {
    fetchLiUserPages();
  }, [fetchLiUserPages]);

  return (
    <Drawer
      anchor={anchor}
      open={open}
      onClose={(_, reason) => !isBackDropOrEscapeKey(reason) && closeDrawer()}
      sx={{
        zIndex: "modal",
      }}
    >
      <DrawerWrapper sx={{ width: { xs: "100vw", md: 360 } }}>
        <ContentHeader>
          <IconButton
            onClick={() => closeDrawer()}
            sx={{ width: 32, height: 32 }}
          >
            <LeftArrow />
          </IconButton>
          <HeaderTitle>Select Linkedin Page</HeaderTitle>
        </ContentHeader>
        <DrawerInner>
          {showLoading && (
            <LoaderWrapper>
              <CircularProgress />
            </LoaderWrapper>
          )}
          {noUserOrgPagesFound && (
            <p>
              Sorry, we could not find any LinkedIn organization page associated
              with your account.
            </p>
          )}
          {pages.length > 0 && (
            <List>
              {pages.map((page) => (
                <div key={page.orgID}>
                  <ListItem sx={{ p: 0 }}>
                    <ListItemButton
                      onClick={() => setSelected(selected ? null : page.orgID)}
                    >
                      <ListItemImage
                        src={page.image}
                        alt={"LI organization profile image"}
                      />
                      <ListItemText
                        primary={page.name}
                        secondary={
                          page.inUse ? "Page already in use" : undefined
                        }
                      />
                      <ListItemIcon>
                        {selected === page.orgID ? (
                          <CheckCircleOutlined sx={{ color: "#12B0EE" }} />
                        ) : (
                          <CircleOutlined sx={{ color: "#F75D34" }} />
                        )}
                      </ListItemIcon>
                    </ListItemButton>
                  </ListItem>
                  <Divider />
                </div>
              ))}
            </List>
          )}
          <ActionArea>
            <ActionButton
              variant="contained"
              style={{
                marginTop: "40px",
                marginBottom: "30px",
                marginLeft: 0,
              }}
              disabled={disableBtn}
              onClick={handleLISetup}
            >
              {"Link LinkedIn Account"}
            </ActionButton>
          </ActionArea>
        </DrawerInner>
      </DrawerWrapper>
    </Drawer>
  );
};

export default LinkedInLinking;
