import { createContext, FC, useContext, useMemo, useState } from "react";

import {
  IAccounts,
  ILIUser,
  ISmPermission,
  ITWUser,
} from "Shared/Interfaces/SocialMedias";

import {
  SocialMediasApiContextState,
  SocialMediasDataContextState,
} from "./types";

const SocialMediasData = createContext<
  SocialMediasDataContextState | undefined
>(undefined);

const SocialMediasApi = createContext<SocialMediasApiContextState | undefined>(
  undefined
);

const SocialMediasProvider: FC<any> = ({ children }) => {
  const [connectedAccounts, setConnectedAccounts] = useState<IAccounts[]>([]);
  const [unconnectedAccounts, setUnconnectedAccounts] = useState<string[]>([]);
  const [TWUser, setTWUser] = useState<ITWUser | null>(null);
  const [LIUser, setLIUser] = useState<ILIUser | null>(null);
  const [platform, setPlatform] = useState<string | null>(null);
  const [permissions, setPermissions] = useState<ISmPermission>({
    show: false,
    provider: "",
  });
  const data: SocialMediasDataContextState = {
    connectedAccounts,
    unconnectedAccounts,
    TWUser,
    LIUser,
    platform,
    permissions,
  };
  const api = useMemo<SocialMediasApiContextState>(
    () => ({
      setConnectedAccounts,
      setUnconnectedAccounts,
      setTWUser,
      setLIUser,
      setPlatform,
      setPermissions,
    }),
    []
  );

  return (
    <SocialMediasData.Provider value={data}>
      <SocialMediasApi.Provider value={api}>
        {children}
      </SocialMediasApi.Provider>
    </SocialMediasData.Provider>
  );
};

export const useSocialMediasData = () => {
  const context = useContext(SocialMediasData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of SocialMediasProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export const useSocialMediasApi = () => {
  const context = useContext(SocialMediasApi);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of SocialMediasProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default SocialMediasProvider;
