import { AuthenticatedReq } from "Utils/Axios";

export default class SocialMediaApi {
  public static getSetupInfo = async () => {
    return await AuthenticatedReq(`/platforms/sm-connected-platform`, "GET");
  };

  public static getBusinessFBPages = async () => {
    return await AuthenticatedReq(`/fb/facebook-pages`, "GET");
  };

  public static saveFBUserData = async (businessId: number, data: any) => {
    return await AuthenticatedReq(`/fb/pages/${businessId}`, "POST", {
      data,
    });
  };

  public static saveFBPageData = async (data: any) => {
    return await AuthenticatedReq(`/fb/account`, "POST", {
      data,
    });
  };

  public static getIgAccountData = async () => {
    return await AuthenticatedReq(`/ig/fetchData`, "GET");
  };

  public static saveIgAccountData = async (data: any) => {
    return await AuthenticatedReq(`/ig/account`, "POST", {
      data,
    });
  };

  public static unlinkSocialMediaAccount = async (data: any) => {
    return await AuthenticatedReq(`/platforms/unlinkAccount/`, "DELETE", {
      data,
    });
  };

  public static getLiUserPages = async (data: any) => {
    return await AuthenticatedReq(`/setup/linkedin/pages`, "POST", { data });
  };

  public static setLinkedinBusiness = async (data: any) => {
    return await AuthenticatedReq(`/setup/linkedin/setup-page`, "POST", {
      data,
    });
  };
}
