import { FC, Fragment } from "react";

import { FB_IG, LI, TW } from "Constants/BusinessSettings/SocialMediaLinking";
import {
  PermissionDesc,
  PermissionHeading,
} from "Containers/BusinessSettings/LayoutComponents";

interface IPermissionsProps {
  platform: string;
}

interface IPermission {
  name: string;
  desc: string;
}

const Permissions: FC<IPermissionsProps> = ({
  platform,
}: IPermissionsProps): JSX.Element => (
  <Fragment>
    {(platform === "facebook and instagram" ||
      platform === "facebook" ||
      platform === "instagram" ||
      platform === "") &&
      FB_IG.map(({ name, desc }: IPermission, idx: number) => (
        <Fragment key={idx}>
          <PermissionHeading>{name}</PermissionHeading>
          <PermissionDesc>{desc}</PermissionDesc>
        </Fragment>
      ))}
    {platform === "twitter" &&
      TW.map(({ name, desc }: IPermission, idx: number) => (
        <Fragment key={idx}>
          <PermissionHeading>{name}</PermissionHeading>
          <PermissionDesc>{desc}</PermissionDesc>
        </Fragment>
      ))}
    {platform === "linkedin" &&
      LI.map(({ name, desc }: IPermission, idx: number) => (
        <Fragment key={idx}>
          <PermissionHeading>{name}</PermissionHeading>
          <PermissionDesc>{desc}</PermissionDesc>
        </Fragment>
      ))}
  </Fragment>
);

export default Permissions;
